import React from "react";
import "./PopOver.css";
function PopOver({ data }) {
  return (
    <div className="popover-main-box">
      <p>Material Id : {data?.custom_material_id ?? "--"}</p>
      <p>Material Name: {data?.material_name ?? "--"}</p>
      <p>Material Description: {data?.material_description ?? "--"}</p>
      <p>PLM material id : {data?.plm_material_id ?? "--"}</p>
      <div className="arrow-box"></div>
    </div>
  );
}

export default PopOver;
